.slider__container {
  margin: 0 auto;
}
.slick-slide div {
  outline: none;
}
.slick-next,
.slick-prev {
  display: none !important;
}
@media (min-width: 992px) {
  .slick-next,
  .slick-prev {
    display: block !important;
  }
}
.slick-prev:before,
.slick-next:before {
  font-size: 30px;
  opacity: 0.35;
}
.slick-next {
  right: 20px;
}
.slick-prev {
  left: 10px;
  z-index: 1;
}
.slick-dots li button:before {
  color: #6c63ff;
}
.slick-dots li.slick-active {
  -webkit-transform: scale(1.3);
  transform: scale(1.3);
}
.slick-dots li.slick-active button:before {
  color: #6c63ff;
}
@media (max-width: 600px) {
  .slick-dots li {
    margin: 0;
    width: 17px;
  }
}
@media (min-width: 992px) and (max-width: 1200px) {
  .slick-dots li {
    margin: 0;
  }
}

.faq__container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  max-width: 70ch;
  margin-top: 60px;
  width: 100%;
}
@media (min-width: 992px) {
  .expanded__inner.expanded__inner__one_col .faq__container {
    max-width: 80ch;
  }
}
.faq__card {
  padding: 12px 10px 12px 0;
  border-bottom: 1px solid hsl(0, 0%, 90%);
  -webkit-transition: background-color 250ms linear,
    -webkit-box-shadow 250ms ease-out;
  -webkit-transition: background-color 250ms linear, box-shadow 250ms ease-out;
  transition: background-color 250ms linear, box-shadow 250ms ease-out;
}
.faq__card:first-child {
  border-top: 1px solid hsl(0, 0%, 90%);
}

.faq__question {
  color: hsl(0, 0%, 33%);
  font-weight: bold;
  font-size: 1.1em;
  position: relative;
  margin: 0.2em 30px 0 0;
  cursor: pointer;
  -webkit-transition: all 250ms linear;
  transition: all 250ms linear;
  white-space: pre-line;
}
.faq__card.opened .faq__question,
.faq__card.opened .faq__answer {
  color: hsl(0, 0%, 5%);
}
.faq__question_caret {
  position: absolute;
  right: -25px;
  width: 16px;
  top: 5px;
}
.faq__answer {
  max-height: 0;
  margin: 0;
  overflow: hidden;
  -webkit-transition: all 200ms ease;
  transition: all 200ms ease;
  white-space: pre-line;
}
.faq__card.opened .faq__answer {
  max-height: 1000px;
  margin: 10px 0 0 0;
}

.forecast__sunny {
  width: 60px;
  height: 60px;
  position: relative;
}
.forecast__sunny::before {
  content: "";
  display: block;
  width: 60px;
  height: 60px;
  background: #fdf6c6;
  border: 1px solid #fde850;
  border-radius: 50%;
  -webkit-animation-duration: 1.6s;
  animation-duration: 1.6s;
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.forecast__sunny::after {
  width: 40px;
  height: 40px;
  background: #fde850;
  border-radius: 50%;
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  margin-left: -20px;
  top: 50%;
  margin-top: -20px;
  border: 1px solid #f5d76a;
}

.forecast__cloudy {
  position: relative;
}

.forecast__cloudy__sun {
  width: 40px;
  height: 40px;
  background: #faeb7c;
  border-radius: 50%;
  border: 1px solid #f5d76a;
  margin-left: 10px;
}

.forecast__cloudy__cloud,
.forecast__rainy__cloud,
.forecast__snow__cloud {
  width: 30px;
  height: 25px;
  top: 50%;
  margin-top: -10px;
  background: white;
  position: absolute;
  border: 1px solid #7ccafa;
  border-top-right-radius: 50%;
  border-top-left-radius: 50%;
}

.forecast__cloudy__cloud:after,
.forecast__rainy__cloud:after,
.forecast__snow__cloud:after {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  background: white;
  border-top: 1px solid #7ccafa;
  border-left: 1px solid #7ccafa;
  border-radius: 50%;
  position: absolute;
  left: -10px;
  bottom: 0px;
}

.forecast__cloudy__cloud:before,
.forecast__rainy__cloud:before,
.forecast__snow__cloud:before {
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  background: white;
  border-top: 1px solid #7ccafa;
  border-right: 1px solid #7ccafa;
  border-radius: 50%;
  position: absolute;
  right: -9px;
  bottom: 0px;
}

.forecast__cloudy__cloud--normal {
  -webkit-animation-duration: 6s;
  animation-duration: 6s;
  -webkit-animation-name: moveRight;
  animation-name: moveRight;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.forecast__cloudy__cloud--small {
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  left: 50px;
  margin-top: -20px;
  -webkit-animation-duration: 6s;
  animation-duration: 6s;
  -webkit-animation-name: moveLeft;
  animation-name: moveLeft;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
}

.forecast__rainy,
.forecast__snow {
  height: 50px;
  width: 60px;
  overflow: hidden;
  position: relative;
}

.forecast__rainy__cloud--grey {
  top: 10px;
  border: 0px;
  margin-left: 15px;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
    from(#7f868d),
    to(#466d98)
  );
  background: -webkit-linear-gradient(top, #7f868d, #466d98);
  background: linear-gradient(to bottom, #7f868d, #466d98);
}

.forecast__rainy__cloud--grey:after {
  border-top: 1px solid #7f868d;
  border-left: 1px solid #7f868d;
  background: -webkit-gradient(
    linear,
    left top,
    left bottom,
