/* animations */
@-webkit-keyframes fadeIn {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}
@keyframes fadeIn {
  0% {
    opacity: 1;
  }

  50% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes moveRight {
  0% {
    left: 10px;
  }

  50% {
    left: 45px;
  }

  100% {
    left: 10px;
  }
}

@keyframes moveRight {
  0% {
    left: 10px;
  }

  50% {
    left: 45px;
  }

  100% {
    left: 10px;
  }
}

@-webkit-keyframes moveLeft {
  0% {
    left: 50px;
  }

  50% {
    left: 10px;
  }

  100% {
    left: 50px;
  }
}

@keyframes moveLeft {
  0% {
    left: 50px;
  }

  50% {
    left: 10px;
  }

  100% {
    left: 50px;
  }
}

@-webkit-keyframes raining {
  0% {
    top: 15px;
  }
  100% {
    top: 50px;
  }
}

@keyframes raining {
  0% {
    top: 15px;
  }
  100% {
    top: 50px;
  }
}

